import React from 'react';
import './index.css';
import {motion} from 'framer-motion';

const childVariants = {
    hidden: {
      opacity: 0,
    },
    visible: {
      opacity: 1,
      transition:{
        duration: 2,
      }
    }
  }

function Loader() {
    return (
        <motion.div  className="container-loader" variants={childVariants} initial="hidden" animate="visible" exit={{ opacity:0,transition:{duration:2} }}>
            IICJEC
        </motion.div>
    );
}

export default Loader;